
















































































import Vue from "vue";
import { Menu, MenuItem } from "../models/menu";

import { Display } from "../configuration/display";

export default Vue.extend({
  props: {
    menu: Menu
  },
  data: () => ({
    drawer: null,
    isMobile: Display.isMobile()
  }),
  methods: {
    sair() {
      this.$emit("sair");
    },
    changePass() {
      this.$emit("changePass");
    }
  }
});
